.Snippet {
  padding: 5px 10px 10px 10px;
  background-color: white;

  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
  border: solid black 2px;

  display: flex;
  flex-direction: column;

  .deleteSnippet-btn {
    outline: none;
    margin-left: auto;
    cursor: pointer;
  }
  p {
    text-align: center;
    padding-bottom: 10px;
    font-weight: bold;
  }
}
